//  echarts  echarts 
import * as echarts from 'echarts/core';
//  Chart
import {LineChart } from 'echarts/charts';
//  Component
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  LegendComponent,
} from 'echarts/components';
// 
import { LabelLayout, UniversalTransition } from 'echarts/features';
//  Canvas  CanvasRenderer  SVGRenderer 
import { CanvasRenderer } from 'echarts/renderers';

// 
echarts.use([
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  LabelLayout,
  UniversalTransition,
  CanvasRenderer,
  LegendComponent,
  LineChart,
]);


function fontSize(res) {
    let clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
    if (!clientWidth) return;
    let fontSize = 100 * (clientWidth / 1920);
    return res * fontSize;
  }

let eLineConfig_a = (...arr) =>{
    let optionL
    optionL = {
      grid: {
        left: '5%',
        right: '5%',
        top: '20%',
        bottom: '10%',
        containLabel: true
      },
      tooltip: {
        padding: [50, 50],
        trigger: 'axis',
        extraCssText: 'width:4vw;height:4vh;background:#053E6E;border:none;border-radius:1rem;',
        textStyle: {
          "fontSize": fontSize(0.10),
          "color": '#fff'
        },
        // axisPointer: {
        //   type: 'shadow'
        // }
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        axisLabel: {
          show: true,
          fontFamily: '',
          color: "#8EACE2",
          margin:fontSize(0.10),
          fontSize: fontSize(0.10)//28px0.28
        },
        grid: {
            x: 10,
            y: 1,
            x2: 0,
            y2: 0,
        },
        splitLine: { show: true, lineStyle: { color: ['#0079D2'], opacity: .2, width: 1, type: 'solid' } },
        data: arr[0].name
      },
      yAxis: {
        type: 'value',
        axisLabel: {
          show: true,
          fontFamily: '',
          color: "#8EACE2",
          fontSize: fontSize(0.10)//28px0.28
        },
        splitLine: { show: false },
      },
      legend: {
        itemWidth: 50,
        orient: 'horizontal',
        icon:'rect',
        data:[{
          name:'',
          textStyle: {fontWeight: 'normal', color: '#22FFE2'}
        },{
          name: '',
          textStyle: {fontWeight: 'normal', color: '#A68BFF'}
          }],
        x:'center',      //
        y:'bottom',     //
        padding:[1,0,0,0],   //[]
        itemGap: 200,
        itemHeight: 20,
        textStyle: {
          "fontSize": fontSize(0.10),
          "padding": 20
        },
      },
      series: [
        {
          name: '',
          data: arr[1].value,
          type: 'line',
          lineStyle:{
            width:5,
            color:'#A68BFF'
          },
          smooth: true,
          areaStyle: {
            color: {
              type: 'linear',
              x: 0,
              y: 1,
              x2: 0,
              y2: 0,
              colorStops: [
                {
                  offset: .1,
                  color: '#011941' // 0% 
                },
                {
                  offset: 1,
                  color: '#52CBF0' // 100% 
                }
              ],
              global: false //  false
            }
          }
        },
        {
          name: '',
          data: arr[0].value,
          type: 'line',
          lineStyle:{
            width:5,
            color:'#22FFE2'
          },
          smooth: true,
          areaStyle: {
            color: {
              type: 'linear',
              x: 0,
              y: 1,
              x2: 0,
              y2: 0,
              colorStops: [
                {
                  offset: .1,
                  color: '#011941' // 0% 
                },
                {
                  offset: 1,
                  color: '#52CBF0' // 100% 
                }
              ],
              global: false //  false
            }
          }
        },
      ]
    }
    return optionL
  }
  

export default function eChartsDetail(objArray) {
    let lintFt_a =  echarts.init(objArray[0]);
    let optionLa = eLineConfig_a({name:[
      '12.9',
      '12.10',
      '12.11',
      '12.12',
      '12.13'
    ],value:[
      10, 30, 80, 20, 10
    ]},{value:[
      5,20,15,80,20
    ]});
    optionLa && lintFt_a.setOption(optionLa); 
}